@import "../src/styles/global";

/*@formatter:off*/
$theme-custom: (
        "red":(
                colorPrimary: #B557BF,                                          //
                colorPrimaryLight: #EE46FF,                                     //
                colorPrimaryDeep: #9817A6,                                      //
                colorPrimaryLighter: #ffedfd,                                   //
                colorSuccess: #4DDE00,                                          //
                colorSuccessLight: #99EE6B,                                     //
                colorSuccessDeep: #329000,                                      //
                colorWarn: #FF8700,                                             //
                colorWarnLight: #FFBD73,                                        //
                colorWarnDeep: #BF7C30,                                         //
                colorError: #FF1300,                                            //
                colorErrorLight: #FF7D73,                                       //
                colorErrorDeep: #BF3A30,                                        //
                colorInfo: #929292,                                             //
                colorInfoLight: #C8C8C8,                                        //
                colorInfoDeep: #6D6D6D,                                         //
                colorDisabled: #333333,                                         //
                colorDisabledLight: #999999,                                    //
                colorDisabledDeep: #211111,                                     //

                colorContent:#666,                                              //
        )
);
/*@formatter:on*/

@mixin themeWrap {
  @include plThemeWrap($theme-custom) {
    @content;
  }
}